@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap");
.App {
  width: 100%;
  height: 100%;
  font-family: "Rubik", sans-serif;
}

.IPContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.MapContainer {
  display: flex;
  background-color: aquamarine;
  height: 80%;
  width: 100%;
  margin: 0;
  justify-content: center;
  align-items: center;
}

.formContainer {
  display: flex;
  justify-content: center;
  height: 15%;
  margin: 2.5% 0 5%;
}

.formclass {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
  width: 33%;
  
}

.inputfield {
  font-size: 18px;
  font-weight: 400;
  width: 80%;
  height: 80%;
  margin: 0;
  padding: 1px 5px;
  border: none;
  border-radius: 5px 0 0 5px;
  color: hsl(0, 0%, 59%);
}

.inputfield:focus {
  border-top: 0.25em solid rgba(0, 0, 0, 0.5);
  border-bottom: 0.25em solid rgba(0, 0, 0, 0.5);
  border-left: 0.25em solid rgba(0, 0, 0, 0.5);
  outline: none;
  color: hsl(0, 0%, 17%);
  font-weight: 700;
}

.button {
  background-color: black;
  border-radius: 0 5px 5px 0;
  text-align: center;
  height: 87.5%;
  width: 5% ;
  padding: 0 0.25rem;
}

.button:hover {
  background-color: rgba(0, 0, 0, 0.459);
}

.arrowIcon {
  padding: 1.5px 0;
  width: 10px;
  height: 10px;
}

.ContentContainer {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  text-align: center;
}
.ContentWidth {
  width: 100%;
}

.Title {
  color: white;
  margin-top: 5%;
  font-size: 24px;
}
.Background {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -100;
}

.DataContainer {
  margin: 1% 5%;
  display: flex;
  justify-content: space-between;
  background-color: white;
  border-radius: 15px;
}

.contentBox {
  display: inline-block;
  text-align: start;
  width: 25%;
  padding: 2.5% 0 2.5% 2%; 
}

.BoxBoder {
  border-right: 1px solid rgba(0, 0, 0, 0.5);
  display: inline-block;
  width: 0.25px;
  margin-right: 5%;
  height: 70%;
}

.contentframe {
  display: inline-block;
  width: 90%;
}

.contentHeader {
  color: hsl(0, 0%, 59%);
  font-size: 16px;
  margin-bottom: 0;
}
.content {
  margin-top: 1%;
  font-size: 22px;
  color: hsl(0, 0%, 17%);
}

.BackgroundHeader {
  display: flex;
  object-fit: cover;
  margin: 0;
  width: 100%;
  height: 33%;
  padding: 0;
}

.pointer {
  width: 25px;
  height: 30px;
}

#map {
  position: relative;
  z-index: -1;
  height: 600px;
}


@media screen and (max-width: 1150px) {

  .Title {
    margin-top: 1%;
  }
  .formContainer{
    height: 10%;
    margin: 2.5% 0 2.5%; 
  }
  .formclass{
    width: 100%;
  }
  .DataContainer {
    display: block;
  }
  .contentBox {
    width: 100%;
    padding: .5% 1%;
    text-align: center;
  }
  .BoxBoder {
    display: none
  };

}


@media screen and (max-width: 775px) {

 .contentHeader{
   font-size: 12px;
 }
 .content{
   font-size: 16px;
 }

}
